.products {
  display: block;
  padding-top: 121px;
  padding-bottom: 98px;
  // @media screen and (min-width: 480px) {
  //   width: 280px;
  // }

  @media screen and (min-width: $tablet) {
    padding-bottom: 112px;
  }
  @media screen and (min-width: $desktop) {
    padding-top: 127px;
    padding-bottom: 119px;
  }
}
.products__label {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;

  line-height: 1.17;
  margin-bottom: 15px;
  text-align: center;
  text-transform: uppercase;

  color: #ffa5ba;
  @media screen and (min-width: $tablet) {
    font-weight: normal;
    font-size: 26px;
    line-height: 1.15;
    @media screen and (min-width: $desktop) {
      font-size: 30px;
      line-height: 1.13;
    }
  }
}

.products__subtitles {
  margin-bottom: 145px;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #d41443;

  @media screen and (min-width: $tablet) {
    font-size: 48px;
  }
  @media screen and (min-width: $desktop) {
    font-size: 58px;
    line-height: 1.14;
    letter-spacing: 0.06em;

    margin-bottom: 259px;
  }
}

.products__list {
  list-style: none;

  @media screen and (min-width: $tablet) {
    display: flex;
    justify-content: center;
  }
}
.products__item {
  display: inline-block;

  background: #ffb8ca;
  border-radius: 24px;
  padding: 160px 40px 40px;
  @media screen and (max-width: 319px) {
    width: 100%;
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 280px;
  }

  @media screen and (min-width: $tablet) {
    padding: 160px 11px 40px;
    flex-basis: calc(100% / 3 - 40px);
  }
  @media screen and (min-width: $desktop) {
    padding: 163px 44px 50px;
    flex-basis: calc(100% / 3 - 60px);
  }
}
.products__item--first {
  background: #f0d1a5;
}
.products__item--second {
  background: #c2e297;
}

@media screen and (max-width: 767px) {
  .products__item:not(:last-child) {
    margin-bottom: 130px;
  }
}

@media screen and (min-width: $tablet) {
  .products__item:not(:last-child) {
    margin-right: 20px;
  }
}
@media screen and (min-width: $desktop) {
  .products__item:not(:last-child) {
    margin-right: 30px;
  }
}

.products__name {
  position: relative;

  margin-bottom: 60px;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffffff;
  @media screen and (min-width: $desktop) {
    font-size: 30px;
    line-height: 1.13;
  }
}
.products__name::before {
  content: '';
  position: absolute;

  width: 249px;
  height: 252px;
  background-image: url('../images/products-first-mob.png');
  background-size: 249px 252px;
  bottom: 45px;
  left: 50%;
  transform: translate(-50%, 0);
  @media screen and (min-width: $tablet) {
    width: 220px;
    height: 226px;
    background-image: url('../images/products-first-tab.png');
    background-size: 220px 226px;
    bottom: 53px;
    left: 50%;
    transform: translate(-50%, 0);
  }
  @media screen and (min-width: $desktop) {
    width: 335px;
    height: 335px;
    background-image: url('../images/products-first.png');
    background-size: 335px 335px;
    bottom: 72px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}
@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .products__name {
    background-image: url('../images/products-first-mob@2x.png');

    @media screen and (min-width: $tablet) {
      background-image: url('../images/products-first-tab@2x.png');
    }
    @media screen and (min-width: $desktop) {
      background-image: url('../images/products-first@2x.png');
    }
  }
}

.products__name--first::before {
  content: '';
  position: absolute;

  width: 249px;
  height: 252px;
  background-image: url('../images/products-second-mob.png');
  background-size: 249px 252px;
  bottom: 45px;
  left: 50%;
  transform: translate(-50%, 0);
  @media screen and (min-width: $tablet) {
    width: 220px;
    height: 226px;
    background-image: url('../images/products-second-tab.png');
    background-size: 220px 226px;
    bottom: 53px;
    left: 50%;
    transform: translate(-50%, 0);
  }
  @media screen and (min-width: $desktop) {
    width: 335px;
    height: 335px;
    background-image: url('../images/products-second.png');
    background-size: 335px 335px;
    bottom: 72px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}
@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .products__name--first {
    background-image: url('../images/products-second-mob@2x.png');

    @media screen and (min-width: $tablet) {
      background-image: url('../images/products-second-tab@2x.png');
    }
    @media screen and (min-width: $desktop) {
      background-image: url('../images/products-second@2x.png');
    }
  }
}
.products__name--second::before {
  content: '';
  position: absolute;
  width: 249px;
  height: 252px;
  background-image: url('../images/products-third-mob.png');
  background-size: 249px 252px;
  bottom: 45px;
  left: 50%;
  transform: translate(-50%, 0);
  @media screen and (min-width: $tablet) {
    width: 220px;
    height: 226px;
    background-image: url('../images/products-third-tab.png');
    background-size: 220px 226px;
    bottom: 53px;
    left: 50%;
    transform: translate(-50%, 0);
  }
  @media screen and (min-width: $desktop) {
    width: 335px;
    height: 335px;
    background-image: url('../images/products-third.png');
    background-size: 335px 335px;
    bottom: 72px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}
@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .products__name--second {
    background-image: url('../images/products-third-mob@2x.png');
    @media screen and (min-width: $tablet) {
      background-image: url('../images/products-third-tab@2x.png');
    }
    @media screen and (min-width: $desktop) {
      background-image: url('../images/products-third@2x.png');
    }
  }
}

.products__name::after {
  position: absolute;
  display: block;
  content: '\2026';
  top: 35px;
  left: 50%;
  transform: translate(-50%, 0);
  @media screen and (min-width: $desktop) {
    top: 40px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}

.products__text {
  width: 200px;

  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.52;
  text-align: center;
  letter-spacing: 0.04em;
  color: #ffffff;
  margin-bottom: 30px;
  @media screen and (min-width: $tablet) {
    width: 198px;

    line-height: 1.76;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    margin-bottom: 40px;
  }
  @media screen and (min-width: $desktop) {
    width: 241px;
    font-size: 16px;
    line-height: 1.76;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    margin-bottom: 62px;
  }
}
.products__button {
  position: relative;

  display: block;
  margin-right: auto;
  margin-left: auto;

  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
  border: 1px solid transparent;
}
